import React, { FC, useCallback, useEffect } from "react";
import { useGetLocale } from "providers";

import { useLocalStorage } from "hooks";
import { Icon, SwitchBox } from "components";

import { useHotKeys } from "components/HotKeys/Provider";

import { Section } from "../";
import css from "./style.module.scss";

interface Props {
  onClose: () => void;
}

const Content: FC<Props> = ({ onClose }) => {
  const intl = useGetLocale();

  const { isHotkeyActive, setIsHotkeyActive } = useHotKeys();
  const [isEnable, setIsEnable] = useLocalStorage("isHotkeysEnabled");

  useEffect(() => {
    if (isEnable) {
      const is: boolean = JSON.parse(isEnable);

      setIsHotkeyActive(is);
    }
  }, []);

  const changeSwitch = useCallback((action: boolean) => {
    setIsEnable(JSON.stringify(action));
    setIsHotkeyActive(action);
  }, []);

  return (
    <div className={css.root}>
      <div className={css.header}>
        <span>
          {intl.formatMessage({
            id: "hotKeys.title",
          })}
        </span>
        <Icon
          variant="close"
          onClick={onClose}
          fontSize={12}
          className={css.closeIcon}
        />
      </div>
      <Section
        button={"Space"}
        text={intl.formatMessage({
          id: "blackjack.hotkeys.space",
        })}
      />
      <Section
        button={"Q"}
        text={intl.formatMessage({
          id: "blackjack.hotkeys.q",
        })}
      />
      <Section
        button={"W"}
        text={intl.formatMessage({
          id: "blackjack.hotkeys.w",
        })}
      />
      <Section
        button={"E"}
        text={intl.formatMessage({
          id: "blackjack.hotkeys.e",
        })}
      />
      <Section
        button={"R"}
        text={intl.formatMessage({
          id: "blackjack.hotkeys.r",
        })}
      />
      <Section
        button={"T"}
        text={intl.formatMessage({
          id: "blackjack.hotkeys.t",
        })}
      />
      <Section
        button={"Y"}
        text={intl.formatMessage({
          id: "blackjack.hotkeys.y",
        })}
      />
      <Section
        button={"A"}
        text={intl.formatMessage({
          id: "blackjack.hotkeys.a",
        })}
      />
      <Section
        button={"S"}
        text={intl.formatMessage({
          id: "blackjack.hotkeys.s",
        })}
      />
      <Section
        button={"D"}
        text={intl.formatMessage({
          id: "blackjack.hotkeys.d",
        })}
      />

      <SwitchBox
        text={{
          on: intl.formatMessage({
            id: "hotKeys.switch.on",
          }),
          off: intl.formatMessage({
            id: "hotKeys.switch.off",
          }),
        }}
        isChecked={isHotkeyActive}
        switchHandleChange={changeSwitch}
      />
    </div>
  );
};

export default Content;
