import React from "react";

import { DialogActions, Icon, iconHeight, Typography } from "components";

import css from "./style.module.scss";

export const headerHeight =
  parseInt(css.paddingTop) + parseInt(css.paddingBottom) + iconHeight;

export interface DialogHeaderButtonOption {
  key: string;
  label: string;
}

export interface DialogHeaderProps {
  title: string;
  onClose: () => void;
}

const DialogHeader = ({ title, onClose }: DialogHeaderProps) => {
  return (
    <DialogActions className={css.dialogActions}>
      <Typography className={css.title}>{title}</Typography>

      <div className={css.grow} />

      <Icon variant="close" className={css.closeIcon} onClick={onClose} />
    </DialogActions>
  );
};

export default React.memo(DialogHeader);
